<template>
  <template v-if="messages.length > 0">
    <h2>Chat</h2>

    <div class="log" id="chatlog">
      <div class="msg" v-for="(msg, index) in messages" :key="index">
        [<span :style="'color: ' + msg.color + ';'">{{ msg.username }}</span
        >]: {{ msg.text }}
      </div>
    </div>
  </template>
</template>

<script lang="ts">
import { defineComponent } from "vue";
import store from "@/store";

export default defineComponent({
  name: "Chat",
  data() {
    return {
      messages: []
    };
  },
  mounted() {
    const colorForUsername = {};

    const getColorForUsername = (username: string) => {
      if (!(colorForUsername as any)[username]) {
        const colors = ["#7a7aff", "#ff635b", "#ee7b00", "#38da9e"];

        const random = Math.floor(Math.random() * colors.length);

        (colorForUsername as any)[username] = colors[random];
      }

      return (colorForUsername as any)[username];
    };

    this.$websocket.subscribe("/topic/chat", message => {
      store.state.chatinuse = true;
      const msg = JSON.parse(message.body);
      msg.color = getColorForUsername(msg.username);
      (this.messages as any).push(msg);

      this.$nextTick(() => {
        const l = document.getElementById("chatlog");
        if (l) l.scrollTop = l.scrollHeight;
      });
    });
  },
  beforeUnmount() {
    this.$websocket.unsubscribe("/topic/chat");
  }
});
</script>

<style scoped lang="scss">
h2 {
  height: 30px;
  padding: 0;
  margin: 0;
  line-height: 30px;
}

.log {
  overflow-y: scroll;
  height: 175px;
  text-align: left;

  .msg {
    color: lightgray;
    word-break: break-all;
  }
  :nth-child(even) {
    background-color: rgba(255, 255, 255, 0.1);
  }
}
</style>
