<template>
  <h1>Hanze Game Server</h1>
  <pre>
    {{ homepagetext }}
  </pre>
  <button v-on:click="view = 1" :class="view == 1 ? 'pressed' : ''">
    about
  </button>
  <button v-on:click="view = 2" :class="view == 2 ? 'pressed' : ''">
    example login
  </button>
  <button v-on:click="view = 3" :class="view == 3 ? 'pressed' : ''">
    example see who is online
  </button>

  <div class="mt2" v-if="view == 1">
    <p>
      The Game Server consists of this frontend that runs on port 8081 by
      default. <br />
      To play a game on this server, you have to build you own client to talk
      with the text protocol that is explained in the
      <a href="/protocol-newgameserver.txt">protocol.txt</a>
    </p>
    <p>
      The Game Server that lets you play games with the text protocol runs on
      port 7789 by default.
    </p>
    <p>
      You are required to <a href="/login">login</a> to start a tournament.
      <br />
      Playing a game can be done without logging in.
    </p>
  </div>
  <div class="mt2" v-if="view == 2">
    <b> Joining the server, localhost example: </b>
    <p>
      To join this server, create a TCP connection.
    </p>
    <code> $ telnet localhost 7789</code>
    <p>Now type <code>login yourname</code></p>
    <p>
      You will get an <code>OK</code> back from the server and your name will
      appear on the left
    </p>
  </div>
  <div class="mt2" v-if="view == 3">
    <b> See who is online, localhost example: </b>
    <p>
      We start with a TCP connection.
    </p>
    <code> $ telnet localhost 7789</code>
    <p>
      Now type <code>get playerlist</code> <br />
      You will get an <code>OK</code> back from the server and after that you
      will get <code>SVR PLAYERLIST []</code>
    </p>
    <p>
      Now login using <code>login yourname</code> If we now ask the server again
      for the player list <code>get playerlist</code> we get the response:
      <code>OK</code> and <code>SVR PLAYERLIST ["yourname"]</code>
    </p>
  </div>
</template>

<script lang="ts">
import { defineComponent } from "vue";
import store from "@/store";

export default defineComponent({
  name: "Home",
  data() {
    return {
      view: 1 as number,
      homepagetext: "" as string
    };
  },
  computed: {
    websocketConnected() {
      return store.getters.websocketConnected;
    },
    currentURL() {
      return window.location.hostname;
    }
  },
  mounted() {
    document.title = "Game Server | Home";

    this.$http
      .get("/homepagetext")
      .then(res => {
        if (res.data) {
          this.homepagetext = res.data;
        }
      })
      .catch(console.error);
  }
});
</script>

