
import { defineComponent } from "vue";
import store from "@/store";

export default defineComponent({
  name: "Home",
  data() {
    return {
      view: 1 as number,
      homepagetext: "" as string
    };
  },
  computed: {
    websocketConnected() {
      return store.getters.websocketConnected;
    },
    currentURL() {
      return window.location.hostname;
    }
  },
  mounted() {
    document.title = "Game Server | Home";

    this.$http
      .get("/homepagetext")
      .then(res => {
        if (res.data) {
          this.homepagetext = res.data;
        }
      })
      .catch(console.error);
  }
});
