import Axios from "axios";
import SockJS from "sockjs-client";
import Stomp, { Client, Frame } from "webstomp-client";
import { createApp } from "vue";
import App from "./App.vue";
import router from "./router";
import store from "./store";

// TypeScript interface types for global properties
declare module "@vue/runtime-core" {
  interface ComponentCustomProperties {
    $http: typeof Axios;
    $websocket: Client;
    $connected: boolean;
  }
}

const app = createApp(App); // create application

// add global store & router
// TODO: use store to store players and current games ?!
app.use(store);
app.use(router);

const DEV_MODE = process.env.NODE_ENV == "development";
const DEV_MODE_SVR_PORT = 8081;

const protocolStr = location.protocol.startsWith("https")
  ? "https://"
  : "http://";
const svrUrl =
  protocolStr +
  (DEV_MODE ? "localhost:" + DEV_MODE_SVR_PORT : window.location.host);

console.info("Assuming the backend runs on", svrUrl);

// set global config for http requests to server
app.config.globalProperties.$http = Axios;
app.config.globalProperties.$http.defaults.baseURL = svrUrl + "/api";
app.config.globalProperties.$http.defaults.headers.common.Accept =
  "application/json";
app.config.globalProperties.$http.defaults.headers.common[
  "Access-Control-Allow-Origin"
] = "*";

const token = JSON.parse(localStorage.getItem("token")!);
if (token) {
  app.config.globalProperties.$http.defaults.headers.common.Authorization =
    "Bearer " + token;
  store.dispatch("loginAdmin", token);
}

// set global config for websocket connection to server
app.config.globalProperties.$websocket = Stomp.over(
  new SockJS(svrUrl + "/websocket")
);
// disable debug messages in console
app.config.globalProperties.$websocket.debug = () => { null };
app.config.globalProperties.$websocket.connect(
  {},
  (frame: Frame) => {
    store.dispatch("connectWebsocket");
  },
  (error: Error) => {
    // TODO: try again after some interval?!
    store.dispatch("disconnectWebsocket");
    console.error(error);
  }
);

app.mount("#app"); // mount application
