import { createStore } from "vuex";
import axios from "axios";

export default createStore({
  state: {
    websocketConnected: false,
    admin: false,
    warning: "" as string,
    fireworks: false,
    chatinuse: false
  },
  mutations: {
    setWebsocketConnected(state, payload) {
      state.websocketConnected = payload;
    },
    setAdmin(state, payload) {
      state.admin = payload;
    },
    setWarning(state, payload) {
      state.warning = payload;
    }
  },
  actions: {
    connectWebsocket({ commit }) {
      commit("setWebsocketConnected", true);
    },
    disconnectWebsocket({ commit }) {
      commit("setWebsocketConnected", false);
    },
    loginAdmin({ commit }, payload) {
      // TODO add password as payload and check if password is correct before setting to true
      commit("setAdmin", true);
      localStorage.setItem("token", JSON.stringify(payload));
      axios.defaults.headers.common.Authorization = "Bearer " + payload;
    },
    logoutAdmin({ commit }) {
      commit("setAdmin", false);
      localStorage.removeItem("token");
      delete axios.defaults.headers.common.Authorization;
    },
    removeWarning({ commit }) {
      commit("setWarning", null);
    }
  },
  getters: {
    websocketConnected(state) {
      return state.websocketConnected;
    },
    admin(state) {
      return state.admin;
    },
    warning(state) {
      return state.warning;
    }
  },
  modules: {}
});
