<template>
  <template v-if="withPoolsSelector == false">
    <h2>Online players: {{ players.length }}</h2>
    <table
      class="center"
      v-if="players.length"
      border="0"
      cellspacing="0"
      cellpadding="0"
    >
      <tr>
        <th>Playername</th>
      </tr>
      <tr v-for="(player, index) in players" :key="index">
        <td>{{ player.name }}</td>
      </tr>
    </table>

    <p v-else-if="this.loaded">There are no clients / players connected</p>
  </template>
  <template v-else>
    <label>
      Divide selection in
      <input @change="onautodividechange" type="number" name="points" min="0" max="5" value="0">
      pools
    </label>
    <table
      class="center"
      v-if="players.length"
      border="0"
      cellspacing="0"
      cellpadding="0"
    >
      <tr>
        <th>Playername</th>
      </tr>
      <tr v-for="(player, index) in players" :key="index">
        <td>{{ player.name }}</td>
        <td>
          <select
            :id="index"
            @change="onplayerpoolselected"
            :value="getplayerpoolvalue(player.name)"
            :playername="player.name"
          >
            <option value="0" selected>Not</option>
            <option v-for="(item, index) in 5" :key="index" :value="index + 1"
              >Pool {{ index + 1 }}</option
            >
          </select>
        </td>
      </tr>
    </table>

    <p v-else-if="this.loaded">There are no clients / players connected</p>
  </template>
  <p v-if="this.loaded == false">...</p>
</template>

<script lang="ts">
import { defineComponent } from "vue";
import { Player } from "@/types";

export default defineComponent({
  name: "OnlinePlayersList",
  props: {
    withPoolsSelector: {
      type: Boolean,
      default: false
    }
  },
  emits: ["onplayerpoolselected"],
  data() {
    return {
      players: [] as Player[],
      loaded: false,
      playerPoolValues: {} as Map<string, number>
    };
  },

  mounted() {
    this.playerPoolValues = new Map<string, number>();

    // important: subscribe first before http request

    // receive updates about new players
    this.$websocket.subscribe("/topic/onplayerjoined", update => {
      const player: Player = JSON.parse(update.body);
      this.players.push(player);
    });

    // receive updates about leaving players
    this.$websocket.subscribe("/topic/onplayerleft", update => {
      const player: Player = JSON.parse(update.body);
      this.players = this.players.filter(p => p.name != player.name);

      if (this.withPoolsSelector) {
        this.$emit("onplayerpoolselected", [0, player.name]);
      }
    });

    // get the full player list once using a HTTP request:
    this.$http
      .get("/playerlist")
      .then(res => {
        this.players = res.data;
        this.loaded = true;
      })
      .catch(console.error);
  },
  beforeUnmount() {
    this.$websocket.unsubscribe("/topic/onplayerjoined");
    this.$websocket.unsubscribe("/topic/onplayerleft");
  },
  methods: {
    onplayerpoolselected(event: any) {
      const pool = event.target.value;
      const name = event.target.getAttribute("playername");
      this.emitonplayerpoolselected(pool, name)
    },
    emitonplayerpoolselected(pool: any, name: any) {
      this.$emit("onplayerpoolselected", [pool, name]);

      if (
        !this.playerPoolValues.has(name) ||
        this.playerPoolValues.get(name) != pool
      ) {
        this.playerPoolValues.set(name, pool);
      }
    },
    getplayerpoolvalue(name: string) {
      if (!this.playerPoolValues.has(name)) {
        return 0;
      }

      return this.playerPoolValues.get(name);
    },
    onautodividechange(event: any) {
      let playerslength = this.players.length;
      let remainingpools = event.target.value
      let poolsize = Math.floor(playerslength / remainingpools);

      let poolnr = 1;
      let playersinpool = 0;

      for(let i = 0; i < this.players.length; i++) {
        const name = this.players[i].name;

        if(playersinpool < poolsize) {
          this.emitonplayerpoolselected(poolnr, name);
          playersinpool++;
        } else { // current pool full
          playerslength -= playersinpool;
          poolnr++;
          remainingpools--;
          poolsize = Math.floor(playerslength / remainingpools);
          this.emitonplayerpoolselected(poolnr, name);

          playersinpool = 1;
        }
      }
    }
  }
});
</script>
