<template>
  <template v-if="websocketConnected">
    <div class="sidebar" :class="chatinuse ? 'chatinuse' : 'chatnotinuse'">
      <div class="playerlist">
        <OnlinePlayersList />
      </div>
      <div class="chat">
        <Chat />
      </div>
    </div>
  </template>
  <div v-bind:class="websocketConnected ? 'content' : ''">
    <div id="nav">
      <router-link class="pagelink" to="/">Home</router-link> |
      <router-link class="pagelink" to="/games">Games</router-link> |
      <router-link class="pagelink" to="/tournament">Tournament</router-link> |
      <!-- &nbsp; is used pre prefent the word from breaking up on mobile -->
      <router-link to="/login" v-if="!isAdmin">Admin&nbsp;Login</router-link>
      <template v-else>
        <router-link class="pagelink" to="/admin">Admin&nbsp;Panel</router-link> |
        <router-link to="/?" v-on:click="logout">Logout</router-link>
      </template>
    </div>

    <div class="warning" v-if="isWarning()">
      {{ getWarning() }}
      <br />
      <button class="x" v-on:click="removeWarning">close</button>
    </div>

    <DikkeVuurpijl v-if="fireworksStarted()" />

    <router-view />
  </div>
</template>

<script lang="ts">
import { defineComponent } from "vue";
import store from "@/store";
import OnlinePlayersList from "@/components/OnlinePlayersList.vue";
import Chat from "@/components/Chat.vue";
import DikkeVuurpijl from "@/components/DikkeVuurpijl.vue";

export default defineComponent({
  name: "App",
  components: {
    OnlinePlayersList,
    Chat,
    DikkeVuurpijl
  },
  computed: {
    chatinuse() {
      return store.state.chatinuse;
    },
    isAdmin() {
      return store.getters.admin;
    },
    websocketConnected() {
      return store.getters.websocketConnected;
    }
  },
  methods: {
    isWarning() {
      if (
        store.state.warning == null ||
        store.getters.warning == undefined ||
        store.getters.warning == ""
      )
        return false;

      return true;
    },
    getWarning() {
      return store.state.warning;
    },
    removeWarning() {
      store.dispatch("removeWarning");
      store.state.fireworks = false;
    },
    fireworksStarted() {
      return store.state.fireworks;
    },
    logout() {
      store.dispatch("logoutAdmin");
    }
  },
  mounted() {
    // If we do not have a connection reload every 3 to 5 seconds
    setTimeout(function() {
      if (store.getters.websocketConnected == false) {
        location.reload();
      }
    }, 3000 + Math.random() * 2000);

    // based on https://w3programmings.com/detect-internet-explorer-and-display-a-warning-message-to-change-the-browser/
    const userAgent = navigator.userAgent;
    const isIE =
      userAgent.indexOf("MSIE ") > -1 || userAgent.indexOf("Trident/") > -1;

    if (isIE) {
      alert(
        "Internet explorer is not supported. We recommend to use Firefox, Google chrome or Edge."
      );
    }
  }
});
</script>

